<script>
    import request from '../../../static/js/request';
    import request1 from '../../../static/js/request1';//header中有token值请求
    import result from '../../../static/js/result'
    import getParam from '../../../static/js/getParam';
    import wxInfo from '../../../static/js/wxPublicInfo'

    export default {
        data() {
            return {
                headSculpture: '',//头像
                phone: '',//登录用户的手机号
                password: '',//密码
                isShow: false,
                userName: '',//登录用户的名字
                babyName: '',
                memberID: 0,
                studentId: 0,//学生ID
                studentName: '',//学生姓名
                address: '',//地址
                role: this.$store.state.role ? this.$store.state.role : 0,//登录类型2为学生3为家长
                studentTotalScore: 0,//学生总分
                studentArtsScience: '',//学生文理
                studentArtsScienceId: 0,//学生文理0为文科1为理科2为未知
                studentSchoolName: '',//学校名
                studentRank: 0,//学生排名
                parentId: 0,//家长ID
                parentName: '',//家长姓名
                parentPhone: '',//家长手机
                studentPhone: '',//学生手机
                isAgent:false//是否为代理
            }
        },
        mounted() {

        },
        created() {
            //重定向地址
            let REDIRECT_URI = encodeURIComponent(this.$host+'/myInfo.html#/')
            let url = location.href
            /*网页授权*/
            if (localStorage.getItem('openId')) {
                console.log('授权获取信息完毕', this.$store.state.openId)
                this.headSculpture = this.$store.state.headImg
            } else {
                if (url.split('?').length === 1) {
                    console.log('授权')
                    location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${wxInfo.wxAppId}&redirect_uri=${REDIRECT_URI}&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect`
                } else if (getParam('code')) {
                    console.log('获取信息')
                    this.getWxInfo();
                }
            }
            if (localStorage['edit'] == 'true') {
                this.$router.push('/login')
            }
            //this.getUserInfo();
            this.getInfo();

        },

        methods: {
            /**
             * 如已是代理查看代理操作指引
             * **/
            goCaoZuo(){
                this.$router.push('/caoZuo?isOrdinary='+true)
            },
            /**
             *代理注册
             * **/
            goRegAgent() {
                this.$router.push('/reg?phone=' + this.phone + '&name=' + this.userName)
            },
            /**
             *获取微信头像
             * **/
            async getWxInfo() {
                let params = {
                    code: getParam('code')
                }

                let getWxInfo = await request('/api/open/wxPay/getAccountApi',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let info = result(getWxInfo)
                if (!info) return;
                console.log(getWxInfo)
                let wxInfo = JSON.parse(getWxInfo.msg)
                if (!wxInfo.openid) {
                    this.headSculpture = this.$store.state.headImg

                } else {
                    this.$store.commit('setOpenId', wxInfo.openid);
                    this.$store.commit('setHeadImg', wxInfo.headimgurl);
                    this.headSculpture = wxInfo.headimgurl;
                }
            },
            /**
             *获取登录用户信息
             * **/
            async getUserInfo() {
                let params = {
                    access_token: this.$store.state.token
                }

                /*let headers = {
                    contentType:'application/x-www-form-urlencoded',
                    //token:this.$store.state.token

                }*/
                let getUserInfo = await request('/api/token/user/userId',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let info = result(getUserInfo)
                console.log('获取电话号失败信息', getUserInfo)
                // loading.hide()
                if (getUserInfo.code == 500) {
                    this.$store.commit('setPhone', '');
                }
                if (!info) return;
                console.log('获取电话号成功信息', getUserInfo)
                this.$store.commit('setPhone', getUserInfo.msg);
                this.$router.go(-1)
            },
            /**
             *获取代理信息（判断该用户是否为代理）
             * **/
            async getAgentInfo() {
                let params = {
                    access_token: this.$store.state.token,
                    account:this.phone
                }

                let getAgentInfo = await request('/api/token/user/selectUserAgent',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let info = result(getAgentInfo)
                if (!info) return;
                if(getAgentInfo.data){
                    this.isAgent = true;
                }
                console.log(getAgentInfo)

            },
            /**
             *根据手机号登录类型获取信息
             * **/
            async getInfo() {
                let url = '';
                if (this.role == 2) {
                    url = '/api/token/user/selectUserStudentPhone'
                } else if (this.role == 3) {
                    url = '/api/token/user/selectUserParentByPhone'
                }
                let params = {
                    account: this.$store.state.phone,
                    access_token: this.$store.state.token
                }
                let headers = {
                    contentType: 'application/x-www-form-urlencoded',
                    //token:this.$store.state.token

                }
                console.log(this.$store.state.token)
                let myInfo = await request(url,
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let res = result(myInfo)
                if (myInfo.code == 500) {
                    this.$router.push('/login')
                }
                console.log('myInfo', myInfo)
                if (!res) return;
                if (this.$store.state.role == 2) {
                    this.userName = res.studentName;
                    this.studentId = res.studentId;
                    this.phone = res.studentPhone;
                    this.memberID = res.id
                    this.password = res.studentPassword;
                    this.studentTotalScore = res.studentTotalScore
                    this.studentArtsScience = res.studentArtsScience
                    this.studentArtsScienceId = res.studentArtsScienceId
                    this.studentSchoolName = res.studentSchoolName
                    this.studentRank = res.studentRank
                    this.parentName = res.userParents[0].parentName?res.userParents[0].parentName:''
                    this.parentPhone = res.userParents[0].parentPhone?res.userParents[0].parentPhone:''
                } else if (this.$store.state.role == 3) {
                    this.userName = res.parentName;
                    this.phone = res.parentPhone;
                    this.password = res.parentPassword;
                    this.parentId = res.parentId
                    this.getParentInfo(res.parentId)
                }

                console.log(res)
                this.$nextTick(()=>{
                    this.getAgentInfo();
                })
            },
            /**
             *获取家长信息
             * **/
            async getParentInfo(parentId) {
                let params = {
                    parentId: parentId,
                    access_token: this.$store.state.token
                }
                let headers = {
                    contentType: 'application/x-www-form-urlencoded',
                    //token:this.$store.state.token

                }
                console.log(this.$store.state.token)
                let myInfo = await request('/api/token/user/selectUserParentList',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let res = result(myInfo)
                if (myInfo.code == 500) {
                    this.$router.push('/login')
                }
                console.log('myInfo', myInfo)
                if (!res) return;
                for (let item of res) {
                    if (item.parentId == parentId) {
                        console.log(item)
                        this.studentName = item.userStudents[0].studentName
                        this.studentId = item.userStudents[0].studentId
                        this.studentTotalScore = item.userStudents[0].studentTotalScore
                        this.studentArtsScience = item.userStudents[0].studentArtsScience
                        this.studentArtsScienceId = item.userStudents[0].studentArtsScienceId
                        this.studentSchoolName = item.userStudents[0].studentSchoolName
                        this.studentRank = item.userStudents[0].studentRank
                        this.studentPhone = item.userStudents[0].studentPhone
                    }
                }
                console.log(res)
            },

            async getKindergarten() {
                let params = {}

                let getClassInfo = await request('/api/base/kindergarten/select/' + this.kindergartenId,
                    'get', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let info = result(getClassInfo)
                // loading.hide()
                if (!info) return;
                console.log(info)
                this.kindergartenName = info.kindergartenName;
                this.$nextTick(() => {
                    this.getClass();
                })

            },
            async getClass() {
                let params = {
                    kindergartenId: this.kindergartenId
                }
                let getClassInfo = await request('/api/base/class/list',
                    'post', params, 'application/x-www-form-urlencoded')
                // loading.hide()
                let info = result(getClassInfo)
                // loading.hide()
                if (!info) return;
                console.log('班级信息', info)
                for (let item of info) {
                    if (item.id == this.baseClassID) {
                        this.classes = item.className;
                        this.classLevel = item.classLevel;
                        // this.peopleNum = item.studentsNumber;
                    }
                }
            },
            /**
             *修改信息
             * **/
            editInfo() {
                if (this.$store.state.role == 2) {
                    this.$router.push('/editInfo?studentId=' + this.studentId + '&phone=' + this.phone)
                } else if (this.$store.state.role == 3) {
                    this.$router.push('/editInfo?studentId=' + this.studentId + '&parentId=' + this.parentId + '&phone=' + this.phone)
                }

            },
            /**
             * 修改手机号
             * */
            editPhone() {
                if (this.$store.state.role == 2) {
                    this.$router.push('./editPhone?studentId=' + this.studentId)
                } else if (this.$store.state.role == 3) {
                    this.$router.push('./editPhone?parentId=' + this.parentId)
                }

            },
            /**
             * 修改密码
             * */
            editPassword() {
                if (this.$store.state.role == 2) {
                    this.$router.push('./editPassword?studentId=' + this.studentId + '&userPhone=' + this.phone+'&oldPwd='+this.password)
                } else if (this.$store.state.role == 3) {
                    this.$router.push('./editPassword?parentId=' + this.parentId + '&userPhone=' + this.phone+'&oldPwd='+this.password)
                }

            },
            checkParent() {
                this.$router.push('./parentList?studentId=' + this.studentId)
            },
            checkBaby() {
                this.$router.push('./babyList?parentId=' + this.parentId)
            },
            /**
             * 查看参赛作品
             * */
            work() {
                this.$router.push('/activity?studentId=' + this.studentId)
            },
            /**
             * 查看交费记录
             * */
            payRecord() {
                this.$router.push('/payRecord?studentId=' + this.studentId)
            },
            /**
             * 注销账号，退出
             * */
            writeOff() {
                this.$store.commit('setToken', '')
                this.$store.commit('setPhone', '')
                this.$store.commit('setBabyIndex', '')
                weui.toast('已退出', 2000);
                this.$router.push('/login')
            }

        },
        watch: {},
    }
</script>

