import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        token:localStorage['token']?localStorage['token']:'',
        isLogin:localStorage['isLogin']?localStorage['isLogin']:'',
        phone:localStorage['phone']?localStorage['phone']:'',
        babyIndex:localStorage['babyIndex']?localStorage['babyIndex']:'',
        role:localStorage['role']?localStorage['role']:'',
        openId:localStorage['openId']?localStorage['openId']:'',
        headImg:localStorage['headImg']?localStorage['headImg']:'',
        //phone:''
    },
    mutations: {
        setToken:(state,val)=>{
            state.token=val;
            localStorage['token']=val;
        },
        setIsLogin:(state,val)=>{
            state.isLogin=val;
            localStorage['isLogin']=val;

        },
        setPhone:(state,val)=>{
            state.phone=val;
            localStorage['phone']=val;
        },
        setBabyIndex:(state,val)=>{
            state.babyIndex=val;
            localStorage['babyIndex']=val;
        },
        setRole:(state,val)=>{
            state.role=val;
            localStorage['role']=val;
        },
        setOpenId:(state,val)=>{
            state.openId=val;
            localStorage['openId']=val;
        },
        setHeadImg:(state,val)=>{
            state.headImg=val;
            localStorage['headImg']=val;
        },
    },
    actions: {

    }
})
